	protected async run() {
		const [{Main}, {TxtLayer}, {GrpLayer}] = await Promise.all([
			import('./Main'),
			import('./TxtLayer'),
			import('./GrpLayer'),
		]);
		this.hFactoryCls = {
			grp: ()=> new GrpLayer,
			txt: ()=> new TxtLayer,
		};

		this.run = async ()=> {
			this.main?.destroy();
			this.main = await Main.generate(this);	//  this.loadPath() 
		};
		await this.run();
	}
	stop() {
		this.main?.destroy();
		this.main = undefined;
	}


