	flush() {
		//  500ms 
		if (this.#tidFlush) {this.#rsvFlush = true; return}	//  Timeout 

		this.flushSub();	// 
		this.#tidFlush = setTimeout(()=> {
			this.#tidFlush = undefined;
			if (! this.#rsvFlush) return;	// 

			this.#rsvFlush = false;			// 
			this.flush();	//  Timeout 
		}, 500);
	}
